function resolveErrorResponse(error) {
	let msg = "";
	if (error.response && error.response.data && error.response.data.errors && error.response.data.errors.details) {
		for (let i = 0; i < error.response.data.errors.details.length; i++) {
			msg = msg + error.response.data.errors.details[0].message;
			if ((i + 1) < error.response.data.errors.details.length) {
				msg = msg + " - ";
			}
		}
	}
	return (msg === "" ? null : msg);
}

const response_then = (res, errorMsg) => {
	if (res && res.data && res.data) {
		return Promise.resolve(res.data.data);
	} else {
		if (!errorMsg) {
			errorMsg = "Unable to proceed please try again later";
		}
		return Promise.reject(new Error(errorMsg));
	}
};
const response_catch = (error, errorMsg): string => {
	let msg = resolveErrorResponse(error);
	if (msg && msg.indexOf("not updated") !== -1) {
		return Promise.reject(new Error("Nothing to update!"));
	}
	msg = msg || errorMsg || "Unable to proceed please try again later";
	return Promise.reject(new Error(msg));
};

export {response_then, response_catch};

export default response_then;